.App {
  height: 100%;
  max-width: 800px;
  min-width: 300px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App-contents {
  height: 100%;
  width: 100%;
  display: flex;
  /* background-color: red; */
}

.About {
  padding: 16px;
  margin: auto;
}

.About-pic {
  transform: rotate(90deg);
}

.About-title, .About-desc {
  font-family: Roboto, sans-serif;
}

.About-title {
  font-weight: 400;
}

.About-desc {
  font-weight: 400;
  line-height: 1.5em;
  color: #000a;
}

.Contact {
  padding: 16px;
}

.Contact a {
  margin: 16px;
}